import { useEffect, useState } from "react";
import { Board, Grid } from "../../components/board";
import s from "../../components/board.module.css";
import io from "socket.io-client";
import { Button, Label, Input, Table } from "reactstrap";

const ENDPOINT = "http://localhost:5000";

export const Game = (props) => {
	const { player } = props;
	const [game, setGame] = useState(null);
	const [inputs, setInputs] = useState({ roomId: "" });
	const [list, setList] = useState([]);
	const [socket, setSocket] = useState(null);

	useEffect(() => {
		if (socket == null) {
			setSocket(io(ENDPOINT));
		}

		if (socket) {
			socket.on("game", ({ game }) => {
				setGame(game);
			});
		
			socket.on("list", ({ list }) => {
				setList(list);
			});
		
			socket.emit("get-list");
		}
	}, [socket]);

	const onCreateGame = () => {
		socket.emit("create", { pid: player.id });
	};

	const onJoinGame = (room) => {
		const pid = player.id;
		socket.emit("join", { pid, room });
	};

	const checkPlaying = (winner) => {
		winner = winner !== undefined ? winner : player;
		return winner.id === game.players[game.firstPlayerPlaying ? 0 : 1].id;
	};

	const isGameReady = () => {
		return game.players !== undefined && game.players[1] !== null;
	};

	const restart = () => {
		socket.emit("restart", {
			room: game.id,
			pid: player.id,
		});
	};

	const checkRestart = () => {};

	const renderMain = () => {
		const getChess = (player) => {
			if (player !== undefined) {
				const chess = { 0: s.white, 1: s.black };
				return chess[game.players.findIndex((x) => x.id === player.id)];
			}
		};
		return (
			<>
				<div>
					Players: <br />
					<table
						style={{
							margin: "auto",
							paddingLeft: "2.5rem",
							borderCollapse: "collapse",
						}}
					>
						{game.players.map((p) => (
							<tr
								key={p.id}
								style={
									p.id === player.id
										? { border: "1px black solid" }
										: {}
								}
							>
								<td key={"name"}>
									<span>{p.name}</span>
								</td>
								<td key={"chess"}>
									<Grid chess={getChess(p)} />
								</td>
								<td key={`playing`} style={{ width: "4rem" }}>
									<span>
										{checkPlaying(p) &&
											!game.gameover &&
											"Playing"}
										{checkPlaying(p) &&
											game.gameover &&
											"Winner"}
										{checkRestart(p) &&
											"Waiting For New Game"}
									</span>
								</td>
							</tr>
						))}
					</table>
				</div>
				<Board
					player={player}
					game={game}
					socket={socket}
					playing={checkPlaying}
				/>
				<div style={{ display: "inline-flex" }}>
					<button onClick={restart} disabled={!game.gameover}>
						Restart
					</button>
				</div>
			</>
		);
	};

	const renderNewGame = () => {
		return (
			<div style={{ margin: "10rem auto", width: "fit-content" }}>
				<Label style={{ marginRight: "1rem" }}>Player</Label>
				<Input disabled value={player.name} />
				<br />
				<Button style={{ width: "100%" }} onClick={onCreateGame}>
					Create Game
				</Button>
				<br />
				<br />
				Or Join a room: <br />
				<Label style={{ marginRight: "1rem" }}>Room Id:</Label>
				<Input
					onChange={(e) => {
						setInputs({ roomId: e.target.value });
					}}
					value={inputs.roomId}
				></Input>
				<Button
					style={{ width: "100%" }}
					onClick={() => onJoinGame(inputs.roomId)}
				>
					Join Room
				</Button>
				<Table style={{ margin: "auto" }}>
					<tbody>
						{list.map((room) => (
							<tr>
								<td>{room.players[0].name}</td>
								<td>
									<Button
										style={{ width: "100%" }}
										onClick={() => onJoinGame(room.id)}
									>
										Join Room
									</Button>
								</td>
							</tr>
						))}
					</tbody>
				</Table>
			</div>
		);
	};

	const renderWaiting = () => {
		if (game !== null) {
			return (
				<div style={{ margin: "10rem auto", width: "fit-content" }}>
					{game.id}
				</div>
			);
		}
	};

	if (game === null) {
		return renderNewGame();
	}
	if (isGameReady()) return renderMain();
	return renderWaiting();
};
