import React from "react";
import s from "./board.module.css";

export const Board = (props) => {

  const {playing, socket, game, player} = props
	const play = (x, y) => {
		if (playing()) {
			socket.emit("play", {
				room: game.id,
				player: player.id,
				x: x,
				y: y,
			});
			return playing();
		}
	};

	const getChess = (x, y) => {
		const chess = { 0: s.white, 1: s.black };
		if (game !== undefined) {
			const grid = game.board[x][y];
			let c =
				grid === ""
					? ""
					: chess[game.players.findIndex((x) => x.id === grid)];
			return c;
		}
	};

	return (
		<>
			<div className={s.board}>
				<div className={s.main}>
					{[...Array(19)].map((_, x) => (
						<div key={`m${x}`} style={{ display: "flex" }}>
							{[...Array(19)].map((_, y) => {
								return (
									<Grid
										key={`m${x}${y}`}
										onclick={() => play(x, y)}
										chess={getChess(x, y)}
									/>
								);
							})}
						</div>
					))}
				</div>
				<div className={s.background}>
					{[...Array(18)].map((_, i) => (
						<div key={`b${i}`} style={{ display: "flex" }}>
							{[...Array(18)].map((_, j) => {
								return <Grid key={`b${i}${j}`} chess="" />;
							})}
						</div>
					))}
				</div>
			</div>
		</>
	);
}

export const Grid = (props) => {

  const play = (ev) => {
    if (!ev.target.classList.contains(s.chess)) {
      ev.target.style.width = "1rem";
      ev.target.style.height = "1rem";
      ev.target.style.margin = "0";
    }
  }

  var classes = `${s.grid}`
  let styles = {}
  if(props.chess) {
    classes += ` ${props.chess} ${s.chess}`
    styles = {
      width: "1rem",
      height: "1rem",
      margin: "0"
    }
  }
  
  return (
    <div
      className={classes}
      style={styles}
      onClick={(ev) => {
        if (props.onclick && props.onclick()) play(ev);
      }}
    ></div>
  );
}